</template>
<script>
    import grafPosts from '../Graf/Post/graf.vue';
    import grafPostsRead from '../Graf/Post/grafPostsReads.vue';

export default {
    components: { grafPosts,grafPostsRead},
    data() {
        return {
            badgeShow:false,
            loader: null,
            loading: false,
            currency_x:null,
            currencys:[],
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
            showDialog: false,
            mealIDShow: null,
            columuns:4,
            showNew:false,
            isSearch:true,
            isLoadingSearch: true,
            search: null,
            show:false,
            mealPeoShow:0,
            postUserId: null,
            meals: [],
            mealName:null,
            pagination: {
                current: 1,
                total: 0
            }
        }
    },
    methods: {
        open(url){
            let routeData = this.$router.resolve({path: url});
            window.open(routeData.href, '_self');

        },
        modfShowDialog(mealId){
            this.showDialog=true;
            this.mealIDShow=mealId;
            //console.log(this.mealIDShow);
        },
        getMeals() {
                this.axios.get('/getPagmMalsChef?page=' + this.pagination.current)
                    .then(response => {
                        this.meals = response.data.data;
                        this.isSearch = false;
                        this.isLoadingSearch = false;
                        this.pagination.current = response.data.current_page;
                        this.pagination.total = response.data.last_page;
                    });
            },
            onPageChange() {
                this.getMeals();
            }
        },
    mounted() {
        this.getMeals();
    },
    changeShow: function(idx) {
      this.list[idx].show = !this.list[idx].show;
    },

    watch: {
        loader () {
            const l = this.loader
            this[l] = !this[l]

            setTimeout(() => (this[l] = false), 3000)

            this.loader = null
        },
        search (val){
            //console.log(val.length);
            if (val.length == 0) return this.getMeals();
            this.isLoadingSearch = true;
            this.axios.get('/getPagmMalsSearchChef/'+val)
                .then(response => {
                    this.meals = response.data.data;
                    this.isSearch = false;
                    this.pagination.current = response.data.current_page;
                    this.pagination.total = response.data.last_page;
                    this.isLoadingSearch = false;
                })
                .catch(() => {
                    this.meals = null;
                    this.isSearch = true;
                    this.isLoadingSearch = false;
                });



        },
    }
}
</script>

